@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&display=swap);
:root {
  --editor-border-color: rgba(255, 255, 255, 0.25);
}

body {
  margin: 0;
  font-family: "Roboto", sans-serif;
}

.pane {
  height: 50vh;
  display: -webkit-flex;
  display: flex;
}

.editor-container {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  -webkit-flex-basis: 0;
          flex-basis: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 0.5rem;
  background-color: black;
  transition: all 0.8s;
}

.editor-container.collapsed {
  -webkit-flex-grow: 0;
          flex-grow: 0;
}

.editor-container.collapsed .CodeMirror-scroll {
  position: absolute;
  overflow: hidden !important;
}

.expand-collapse-btn {
  margin-left: 0.5rem;
  background: none;
  border: none;
  color: white;
  cursor: pointer;
}

.editor-title {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  background-color: hsl(219, 21%, 24%);
  color: white;
  padding: 0.5rem 0.5rem 0.5rem 1rem;
  border: 1px solid rgba(255, 255, 255, 0.25);
  border: 1px solid var(--editor-border-color);
}

.code-mirror-wrapper {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  overflow: hidden;
  border: 1px solid rgba(255, 255, 255, 0.25);
  border: 1px solid var(--editor-border-color);
  border-color: transparent rgba(255, 255, 255, 0.25)
    rgba(255, 255, 255, 0.25) rgba(255, 255, 255, 0.25);
  border-color: transparent var(--editor-border-color)
    var(--editor-border-color) var(--editor-border-color);
}

